
.mainPhoto{
    width: 100%;
}
.main{
width: 100%;
height: 100vh;
background-image: url(../../images/new.png);
 background-size: 100%,100%;
display: flex;
align-items: center;
justify-content: center;
color: #000000;
    font-family: "PT Sans";
}
.main__details{
margin-top: 10%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.main__item{
margin-bottom: 10vh;
}
.wrapInfo{
    display: flex;
    align-items: center;
    flex-direction: column;
}
.detail{
    letter-spacing: 2vh;
    font-size: 1rem;
    line-height: 50px;
    text-align: center;
    font-weight: bold;
}
.aboutAnn{
font-size: 50px;
    text-align: center;
}

.main__instagram{
   color: black;
   text-decoration: none;
   
   font-size: 20px;
}
.main__instagram:hover{
    color: gold;
}
.instagram_wrap{
    width: 250px;
    height: 250px;
    border: 3px solid rgba(146, 146, 146, 0.74);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
}
.instagram__iconlink{
    font-size: 25px;
    color: #1C2430;
    font-weight: 700;
}

@media only screen and (max-width: 484px) {
    .main__item{
        margin-bottom: 20px;
    }
    .main{
        background-size: 120% 50%;
        background-position: top;
        background-repeat: no-repeat;
        padding-top: 170% !important;
    }

}
@media only screen and (max-width: 1200px) {
    .main{
        background-size: 120% 50%;
        background-position: top;
        background-repeat: no-repeat;
        padding-top: 75%;
    }
}