.nav{
    position: fixed;
    width: 100%;
z-index: 100;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding: 2vh;
    text-align: center;
    margin: 0 auto;
    border-bottom: 1px solid black;
    background-color:  rgb(255, 255, 255,0.70)
}
.links{
    text-decoration: none;
    font-size: 1.5rem;
    font-family: "PT Sans";
    color: black;
    transition: 0.5s ease;
    border-bottom: none;
}
.links:hover{
    color: gold;
}
.nav__language{
    color: black !important;
}
.phoneChangeLang{
    width: 40px;
    height: 40px;
    background-color: rgba(165, 165, 165, 0.7);
    position: fixed;
    top: 70%;
    right: 0;
    display: none;
    border-radius:  30px 0 0 30px;
}
.selected{
    transition: 0.5s ease;
    color: gold;
    border-bottom: 1px solid black;
}
@media only screen and (max-width: 484px) {
    .links{
        font-size: 0.8rem;
    }
    .nav__language{
        display: none!important;
    }
    .phoneChangeLang{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .phoneChangeLang p{
        margin-top: 10px;
    }
}
