.contactWrap{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
    width: 100%;
    background-color: #d2d2d2;
}
.contact-form{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 50%;
}
.sendMessage{
    width: 40%;
    margin-top: 20px!important;
}
.form__item{
    width: 100%;
    margin-bottom: 20px!important;
}
.validForm{
    color: green;
}
.notValidForm{
    color: red;
}