
  .mainContainer{
      display: flex;
      justify-content: center;
      width: 100%;
    max-height: 100%;
  padding: 10px;
  flex-direction: column;
transition: 1s ease;
background-image: url("../../images/bcProjects.jpg");
      background-size: 100%, 100%, cover;
      background-repeat: no-repeat;
      
  color: white;
  text-align: center;

  }

  .wrap__project{
  margin-top: 50px;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .project__item{
    width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  }
.item{
margin-bottom: 25px;
  margin-right: 50px;
}
.public{
  display: flex;
  width: 100%;
  padding: 5px;
  background-color: white;
  align-items: center;
  margin-top: 10px;
}
  .public p{
    margin-top: 10px;
    color: #9f9f9f;

  }



  .MuiPaper-root{
  background-color:  rgb(255, 255, 255,0.70) !important;
}
.preloader{
  width: 100%;
  height: 100vh;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 1s ease;
}


.form{
  transition: 1s ease;
  background-color:  rgb(255, 255, 255,0.30);
  padding: 0.2rem;
  font-size: 1rem;
  width: 40%;
margin: 0 auto;
display: flex;
justify-content: center;
align-items: center;
margin-top: 2rem;
}
.form:hover{
  background-color:  rgb(255, 255, 255,0.50);
}
.form a{
  text-decoration: none !important;
  color: black;
  margin: 0 auto;
}
.form p{
  display: flex;
  justify-content: center;
align-items: center;
  margin: 0;
  padding: 5px;
}
.form a:hover{
  text-decoration: underline !important;
  color: gold;
}
@media only screen and (max-width: 484px) {
  .item {
    margin-right: 0!important;
  }

  .mainContainer {
    background-image: none;
  background-color: #e5e5e5;
  }
  .form{
    width: 100%;
    background-color: #b3b0b0;
    margin-top: 1rem;
  }
  .wrap__project{
    margin-top: 50px !important;
  }

}
  @media only screen and (max-width: 1200px) {
    .item {
      margin-right: 20px;
    }
    .wrap__project{
      margin-top: 100px;
    }
  }